import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { LoginComponent } from './login/login.component';
import { AuthenticatedGuard } from './login/authenticated.guard';

const routes: Routes = [
  { path: 'home', loadChildren: './layout/layout.module#LayoutModule' },
  {
    path: 'apps',
    loadChildren: 'app/dictionary/item-input.module#ItemInputModule',
    canActivate: [AuthenticatedGuard],
    data: {
      i18n: {
        isRoot: true
      }
    }
  },
  {
    path: 'login',
    component: LoginComponent,
    data: {
      meta: {
        title: 'PUBLIC.LOGIN.PAGE_TITLE'
      }
    }
  },
  {
    path: '**',
    redirectTo: 'home',
    pathMatch: 'full'
  }
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
